*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter",sans-serif;
}
.app_container{
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.app_container h1{
  font-size: 18px;
  font-weight: 600;
  color: #5B0A21;
  padding: 10px 0;
  border-bottom: 2px solid #f1f1f1;
}
.qr_code_img{
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0,0.1);
  margin-top: 10px;
}
.input-label{
  font-size: 15px;
  color: #5B0A21;
  display: block;
  margin: 10px 0px;
}
input[type="text"]{
  outline: none;
  border:1px solid #5B0A21;
  border-radius:4px;
  width: 100%;
  padding: 10px;
  font-size: 14px;
  margin-bottom: 10px;
}
button{
  color: #fff;
  border: none;
  border-radius:5px;
  padding: 10px 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}
.qr_generator{
  background-color: #5B0A21;
}
.qr_generator:disabled{
  background-color:#f2f2f2;
  cursor: not-allowed;
}
.qr_generator:hover{
  background-color: #680420;
}
.qr_download{
  background-color: #11421C;
  margin-left: 10px;
}
.qr_download:hover{
  background-color: #0b521b;
}